interface IStockToolItemProps {
    data: any;
}

function StockToolItem(props: IStockToolItemProps) {
    const [data, setData] = useState(props.data);
    const [value, setValue] = useState(props.data.symbols.join(","));
    const [loading, setLoading] = useState(false);

    const handleChangeInput = (e: any) => {
        setValue(e.target.value);
    }

    const handleUpdate = () => {
        value && update(value.split(","))
    }

    const handleReset = () => {
        update([])
    }

    const update = async (list: any) => {
        const res = await axios({
            method: "PUT",
            url: `https://restv2.fireant.vn/me/watchlists/${data.watchlistID}`,
            headers: {
                "authorization": "Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6IkdYdExONzViZlZQakdvNERWdjV4QkRITHpnSSIsImtpZCI6IkdYdExONzViZlZQakdvNERWdjV4QkRITHpnSSJ9..oZ8S_sTP6qVRJqY4h7g0JvXVPB0k8tm4go9pUFD0sS_sDZbC6zjelAVVNGHWJja82ewJbUEmTJrnDWAKR-rg5Pprp4DW7MzaN0lw3Bw0wEacphtyglx-H14-0Wnv_-2KMyQLP5EYH8wgyiw9I3ig_i7kHJy-XgCd__tdoMKvarkIXPzJJJY32gq-LScWb3HyZsfEdi-DEZUUzjAHR1nguY8oNmCiA6FaQCzOBU_qfgmOLWhN9ZNN1G3ODAeoOnphLJuWjHIrwPuVXy6B39eU2PtHmujtw_YOXdIWEi0lRhqV1pZOrJEarQqjdV3K5XNwpGvONT8lvUwUYGoOwwBFJg"
            },
            data: {
                name: data.name,
                symbols: list,
                userName: "minhpn.org.ec1@gmail.com",
                watchlistID: data.watchlistID,
            }
        })
        if (res && res.data) {
            setData(res.data)
            setValue(res.data.symbols.join(","))
        }
    }

    const handleFilter = () => {
        setLoading(true)
        getHistorialQuoteAll();
    }

    const getHistorialQuoteAll = () => {
        const listPromises: any = [];
        data.symbols.map((j: any) => {
            listPromises.push(getHistorialQuote(j))
        })
        Promise.all(listPromises).then(res => {
            // console.log(res);
            const list = res.filter((i: any) => i)
            // console.log(93, list)
            update(list)
            setLoading(false)
            notification.success({ message: "success" })
        })
    }

    const getHistorialQuote = async (symbol: string) => {
        if (!symbol) return;
        const startDate = moment().add(-1000, 'days').format(DATE_FORMAT)
        const endDate = moment().add(0, 'days').format(DATE_FORMAT)


        const res = await axios({
            method: "GET",
            headers: {
                "Authorization": "Bearer eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsIng1dCI6IkdYdExONzViZlZQakdvNERWdjV4QkRITHpnSSIsImtpZCI6IkdYdExONzViZlZQakdvNERWdjV4QkRITHpnSSJ9..OhgGCRCsL8HVXSueC31wVLUhwWWPkOu-yKTZkt3jhdrK3MMA1yJroj0Y73odY9XSLZ3dA4hUTierF0LxcHgQ-pf3UXR5KYU8E7ieThAXnIPibWR8ESFtB0X3l8XYyWSYZNoqoUiV9NGgvG2yg0tQ7lvjM8UYbiI-3vUfWFsMX7XU3TQnhxW8jYS_bEXEz7Fvd_wQbjmnUhQZuIVJmyO0tFd7TGaVipqDbRdry3iJRDKETIAMNIQx9miHLHGvEqVD5BsadOP4l8M8zgVX_SEZJuYq6zWOtVhlq3uink7VvnbZ7tFahZ4Ty4z8ev5QbUU846OZPQyMlEnu_TpQNpI1hg"
            },
            url: `https://restv2.fireant.vn/symbols/${symbol}/historical-quotes?startDate=${startDate}&endDate=${endDate}&offset=0&limit=20`,
        })
        if (res.data) {
            const list = res.data.filter((i: any) => {
                return i.totalVolume < MIN_TOTAL_VOLUME || i.totalValue < MIN_TOTAL_VALUE
            })
            if (list.length > 0) {
                return null
            }
            return symbol
        }
        return null
    }

    return <div>
        {data.watchlistID} - {data.name} - {data.symbols.length}
        <Button disabled={loading} style={{ marginLeft: "20px" }} danger onClick={handleReset}>Reset</Button>
        <Button onClick={handleFilter} disabled={loading}>Loc Tong Gia Tri</Button>
        <div style={{ display: "flex" }}>
            <Input
                value={value}
                onChange={handleChangeInput}
            />
            {/* <Button disabled={loading} onClick={handleUpdate}>Update list</Button> */}
        </div>
        <hr />
    </div>
}
